body {
  background-color: #ffffff;
  color: #757575;
  font-family: Titan One;
  font-style: normal;
  font-family: 'DM Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  font-size: 16px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}

.container {
    width: 1280px;
    height: 665px;
    left: 0px;
    top: 0px;
    background: #FFB8CA;
}
@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 460px) {
    .container {
      width: 460px;
    }
  }
  

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}